import React, {Component} from 'react';
import Async from 'react-code-splitting'
import {connect} from 'react-redux';
import {BackTop} from 'antd';

import {ConnectedRouter, routerReducer, routerMiddleware, push} from 'react-router-redux'
import {
    BrowserRouter as Router,
    HashRouter,
    Route,
    Link
} from 'react-router-dom'
import './App.css';
import createHistory from 'history/createBrowserHistory'
import {asyncTest} from './actions';
import Header from './components/Header';
import Footer from './components/Footer';
import Blog from './pages/Blog/index';

const Detail  = () => <Async load={import( './pages/Detail/index')} />;
const AdminDetail  = () => <Async load={import( './pages/AdminDetail/index')} />;
const PostArticle  = () => <Async load={import( './pages/PostArticle')} />;
const Life  = () => <Async load={import( './pages/Life')} />;
const Login  = () => <Async load={import( './pages/Login')} />;
const Test  = () => <Async load={import( './pages/Test/index')} />;
// const Blog  = () => <Async load={import( './pages/Blog/index')} />;
const Admin  = () => <Async load={import( './pages/Admin/index')} />;

// const Home = () => <Async load={import('./Home')} />
const history = createHistory()

// Build the middleware for intercepting and dispatching navigation actions
// const middleware = routerMiddleware(history)

class App extends Component {
    render() {
        const {dispatch, testAsync} = this.props;
        return (
            <ConnectedRouter history={history}>
                <div>
                    <Header />
                    <Route exact path="/" component={Blog}/>
                    <Route path="/Admin" component={Admin}/>
                    <Route path="/PostArticle" component={PostArticle}/>
                    <Route path="/Login" component={Login}/>
                    <Route path="/Life" component={Life}/>
                    <Route path="/Test" component={Test}/>
                    <Route path="/Detail/:id" component={Detail}/>
                    <Route path="/AdminDetail/:id" component={AdminDetail}/>
                    <BackTop />
                    <Footer />
                </div>
            </ConnectedRouter>
        );
    }
}

const select = (state) => {
    return {
        testAsync: state.testAsync
    }
}
export default connect(select)(App);



