import React, {Component} from 'react';
import {Layout, Menu, Breadcrumb, Row, Col} from 'antd';
import {
    Link
} from 'react-router-dom'
import TopTips from '../../components/TopTips';

const {Header, Content, Footer} = Layout;

class TopNav extends Component {
    constructor(){
        super()
        this.state={
            userName:null
        }
    }
    componentWillMount(){
        this.setState({
            userName:localStorage.userName
        })
    }
    logout(){
        localStorage.clear()
    }
    render() {
        return (
            <Layout>
                <Header style={{position: 'fixed', width: '100%', padding: 0, zIndex: 10}}>
                    <Row>
                        <Col span={2}></Col>
                        <Col span={17}>
                            <Menu
                                theme="dark"
                                mode="horizontal"
                                defaultSelectedKeys={['2']}
                                style={{lineHeight: '64px'}}
                            >
                                <Menu.Item key="2">
                                    <Link to="/">
                                        
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="3">

                                    <Link to="/Admin">
                                        
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="4">
                                    <Link to="/Life">
                                        
                                    </Link>
                                </Menu.Item>
                                <Menu.Item key="5">
                                    <Link to="/Test">
                                        
                                    </Link>
                                </Menu.Item>
                            </Menu>
                        </Col>
                        <Col span={3}>
                            {
                                this.state.userName==null?
                                    <Menu
                                        theme="dark"
                                        mode="horizontal"
                                        defaultSelectedKeys={['2']}
                                        style={{lineHeight: '64px'}}
                                    >
                                        <Menu.Item key="6">
                                            <Link to="/Login">
                                                
                                            </Link>
                                        </Menu.Item>

                                    </Menu>
                                    :
                                    <Menu
                                        theme="dark"
                                        mode="horizontal"
                                        defaultSelectedKeys={['2']}
                                        style={{lineHeight: '64px'}}
                                    >
                                        <Menu.Item key="6">
                                            <Link to="/Admin">
                                                {this.state.userName}
                                            </Link>
                                        </Menu.Item>
                                        <Menu.Item key="7" onClick={this.logout.bind(this)}>
                                            
                                        </Menu.Item>

                                    </Menu>
                            }
                        </Col>
                        <Col span={2}/>
                    </Row>
                </Header>
                <Content style={{padding: '0 50px', marginTop: 64}}>
                    <TopTips/>
                </Content>
            </Layout>
        );
    }
}

export default TopNav;



